@import "https://cdn.jsdelivr.net/npm/bootstrap@5.1.3/dist/css/bootstrap.min.css";

#container {
  margin: 1rem;
}

.items {
  margin-bottom: 1rem;
}

.input {
  height: 50px;
  width: calc(100% - 10px);
  border-radius: 20px;
  padding: 15px;
}

.question_links {
  text-decoration: none;
  transition: text-decoration-color 0.5s ease-in-out;
}

.question_links:hover {
  text-decoration: underline;
  text-decoration-color: rgb(8, 180, 172);
}

.space {
  padding: 20px;
}
.related_navbar {
  display: block;
  text-decoration: none;
  text-align: right;
}
.related_navbar:hover {
  text-decoration: underline;
  text-decoration-color: rgb(8, 180, 172);
}
.list {
  display: block;
  padding: 3px;
  text-decoration: none;
  color: inherit;
}

.lists {
  text-align: right;
}
@media (max-height: 732px) {
  #left {
    top: 85px;
    height: 88vh;
    overflow-y: scroll;
    overflow-x: hidden;
  }
}

@media (max-width: 766px) {
  #container {
    display: block;
    flex-wrap: wrap;
    position: relative;
    margin: auto;
    width: 90%;
    padding-left: 15px;
    padding-right: 10px;
  }

  #left {
    display: none;
  }

  #middle {
    padding: 10px;
    margin-top: 5px;
  }

  #right {
    padding-left: 18px;
  }
}

@media (min-width: 767px) and (max-width: 970px) {
  #container {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    margin: auto;
    width: 90%;
    padding: 25px;
    padding-right: 0px;
  }

  #left {
    width: 10%;
    position: -webkit-fixed;
    position: fixed;
    top: 16vh;
    align-self: start;
    /* margin-left: -320px; */
    padding-bottom: 20px;
    text-align: left;
  }
  #leftt {
    display: show;
  }
  /* #bringUp{
        margin-top: -70px;
        text-align: right;
    } */

  #middle {
    padding-top: 20px;
    margin: 0 auto;
    width: 100%;
    margin-left: 10%;
    border-left: 1px solid whitesmoke;
    padding-left: 20px;
  }

  #right {
    /* width: 100%;
    padding: 70px;
    padding-left: 20px;
    margin-left: 20%;
    border-left: 1px solid whitesmoke; */
    display: block;
    width: 100%;
    padding-left: 20px;
    padding-top: 70px;
    margin-left: 10%;
  }

  .input-container {
    padding: 8px;
  }

  .question-list {
    margin-left: 2px;
  }

  .question-list > li {
    padding: 7px;
  }
}

@media (min-width: 971px) and (max-width: 2200px) {
  #container {
    display: flex;
    flex-direction: row;
    left: 50%;
    margin: auto;
    max-width: 90%;
    padding-left: 20px;
    padding-right: 10px;
  }

  #left {
    flex-basis: 30%;
    position: -webkit-sticky;
    position: sticky;
    top: 15vh;
    align-self: flex-start;
    padding-right: 20px;
    padding-bottom: 20px;
    text-align: left;
  }

  #middle {
    flex-basis: 100%;
    margin: 0 auto;
    border-left: 1px solid whitesmoke;
    border-right: 1px solid whitesmoke;
    padding: 30px;
  }

  #right {
    flex-basis: 34%;
    height: auto;
    text-align: right;
    padding: 20px;
  }
  .Right_t {
    flex-basis: 34%;
    height: auto;
    text-align: right;
    padding: 20px;
    margin-top: 0%;
  }

  .input-container {
    padding: 30px;
    padding-top: 0;
    text-align: center;
  }

  .question-list {
    margin-left: 25px;
  }

  #curves div {
    width: 20px;
    height: 15px;
    border-left: 3px solid #999;
  }

  #curves.width div {
    border-color: transparent transparent transparent #999;
  }

  #curve1 {
    -moz-border-radius: 50px 0 0 50px;
    border-radius: 50px 0 0 50px;
  }
}

@media (min-width: 2200px) {
  .fixwidth {
    position: relative;
  }

  #container {
    margin: auto;
    width: 90%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    position: relative;
  }

  #left {
    width: 310px;
    max-width: 310px;
    position: -webkit-sticky;
    position: sticky;
    top: 10vh;
    align-self: flex-start;
    padding-right: 20px;
    padding-bottom: 20px;
    text-align: left;
    border: 1px solid black;
  }

  #middle {
    width: 900px;
    /* or whatever width you want. */
    max-width: 900px;
    /* or whatever width you want. */
    display: inline-block;
    border-left: 1px solid whitesmoke;
    border-right: 1px solid whitesmoke;

    padding: 20px;
  }

  #right {
    width: 430px;
    /* or whatever width you want. */
    max-width: 430px;
    /* or whatever width you want. */
    display: inline-block;

    border: 1px solid black;
    text-align: left;
    padding: 20px;
    padding-top: 0px;
  }

  .input-container {
    padding: 30px;
    padding-top: 0;
    text-align: center;
  }

  .question-list {
    margin-left: 25px;
  }

  #curves div {
    width: 20px;
    height: 15px;
    border-left: 3px solid #999;
  }

  #curves.width div {
    border-color: transparent transparent transparent #999;
  }

  #curve1 {
    -moz-border-radius: 50px 0 0 50px;
    border-radius: 50px 0 0 50px;
  }
}
