      rgb(62, 80, 62) 56%,
      rgb(151, 151, 240) 170%
    );
    word-wrap: break-word;
    border-radius: 8px;
  }

  .testss {
    height: 100%;
    width: calc(100% - 20px);
    border-radius: 3% 90%;
    object-fit: cover;
  }

  .ToWhom > .Third {
    text-align: var(--text-center);
    background-image: linear-gradient(
      rgb(185, 163, 163) 5%,
