      rgb(133, 133, 204) 150%
    );
    word-wrap: break-word;
    border-radius: 8px;
  }
  @keyframes spin {
    0% {
      transform: translateY(4400px);
    }

    40% {
      background-color: aqua;
    }

    70% {
      background-color: rgb(187, 20, 20);
    }

    100% {
      transform: translateY(-1050px);
    }
  }
}
